  display: grid;
}

.grid-recetas li {
  background-color: #fff;
  border-radius: 10px;
  transition: transform .2s;
  overflow: hidden;
  box-shadow: 0 4px 12px #0000001a;
}

.grid-recetas li:hover {
  transform: translateY(-5px);
}

.grid-recetas a {
